* {
  border: 0;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

:focus, :active, a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  width: 100%;
  height: 100%;
  font-family: sans-serif;
  font-size: 14px;
  line-height: 1;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a, a:visited {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
}

.main {
  flex-direction: column;
  width: 100%;
  max-width: 1200px;
  min-height: 100%;
  margin: 0 auto;
  display: flex;
}

.main__title {
  color: #18141498;
  text-align: center;
  letter-spacing: -2px;
  margin-top: 2rem;
  margin-bottom: 2rem;
  font-size: 3rem;
}

@media screen and (width <= 768px) {
  .main__title {
    font-size: 2rem;
  }
}

.main__content {
  flex-direction: column;
  justify-content: space-between;
  gap: 1rem;
  height: calc(100vh - 7rem);
  margin: 0 20px;
  padding-bottom: 3rem;
  display: flex;
}

.main__content-header {
  border-bottom: 1px solid #18141498;
  padding-bottom: .35rem;
}

.main__content-header span {
  color: #fff;
  background-color: #18141498;
  border-top-left-radius: .375rem;
  border-top-right-radius: .375rem;
  padding: .3rem 1rem;
  font-size: 1rem;
}

.main__content-body {
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  display: flex;
}

.main__content-body p {
  text-align: center;
  letter-spacing: -1px;
  color: #1f1f1f;
  font-size: 1.5rem;
  font-weight: 600;
}

.main__cards {
  height: 100%;
  margin-top: 30vh;
  position: relative;
}

.main__cards-scroll-container {
  background: #0000001a;
  width: 6px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (width <= 768px) {
  .main__cards-scroll-container {
    left: 40px;
  }
}

.main__cards-progress-bar {
  background: #c00;
  width: 100%;
  height: 0;
  transition: height .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.main__cards-items {
  flex-direction: column;
  width: 100%;
  max-width: 1200px;
  padding: 20px;
  display: flex;
  position: relative;
  transform: translateY(-230px);
}

@media screen and (width <= 768px) {
  .main__cards-items {
    width: 100%;
    padding-left: 15px;
  }
}

.main__cards-item {
  align-items: center;
  gap: 20px;
  width: 50%;
  height: 400px;
  margin-bottom: 40px;
  transition: all .3s;
  display: flex;
  position: relative;
}

.main__cards-item:nth-child(2n) {
  flex-direction: row-reverse;
  align-self: flex-end;
  transform: translateX(-88px);
}

@media screen and (width <= 768px) {
  .main__cards-item:nth-child(2n) {
    justify-content: flex-end;
    width: 100%;
    transform: translateX(0);
  }
}

.main__cards-item:nth-child(2n) .card {
  margin-right: -40px;
}

@media screen and (width <= 768px) {
  .main__cards-item:nth-child(2n) .card {
    margin-right: unset;
  }
}

.main__cards-item:nth-child(odd) {
  transform: translateX(90px);
}

@media screen and (width <= 768px) {
  .main__cards-item:nth-child(odd) {
    flex-direction: row-reverse;
    justify-content: flex-end;
    width: 100%;
    transform: translateX(0);
  }
}

.main__cards-item:nth-child(odd) .card {
  margin-left: -40px;
}

@media screen and (width <= 768px) {
  .main__cards-item:nth-child(odd) .card {
    margin-left: unset;
  }

  .main__cards-item {
    margin-bottom: 30px;
  }
}

.main__cards-item .card {
  opacity: 0;
  background: #f0f0f0;
  border: 1px solid #f5f5f5;
  border-radius: .625rem;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  max-width: 500px;
  height: 400px;
  padding: 20px;
  transition: opacity .5s, transform .5s cubic-bezier(.175, .885, .32, 1.275);
  display: flex;
  transform: translateY(200px);
  box-shadow: 0 10px 15px -3px #e6e6e666, 0 4px 6px -4px #e6e6e666;
}

@media screen and (width <= 768px) {
  .main__cards-item .card {
    transform: translate(0, 200px);
  }
}

.main__cards-item .card__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  max-height: calc(100% - 60px);
}

.main__cards-item .card.visible.right, .main__cards-item .card.visible.left {
  opacity: 1;
  transform: translateY(0);
}

.main__cards-item .label {
  z-index: 20;
  text-align: center;
  justify-content: space-between;
  align-items: center;
  width: 140px;
  height: 60px;
  font-size: 2rem;
  line-height: 40px;
  display: flex;
}

@media screen and (width <= 768px) {
  .main__cards-item .label {
    width: 100px;
    font-size: 1.5rem;
  }
}

.main__cards-item .label svg {
  fill: inherit;
  background-color: #fff;
}

.main__cards-item .label.filled {
  color: #c00;
  fill: #c00;
}

.main__cards-item .label.left {
  left: -144px;
}

@media screen and (width <= 768px) {
  .main__cards-item .label.left {
    flex-direction: row-reverse;
    left: -54px;
  }
}
